import { Component, OnInit } from '@angular/core';

@Component({
	selector: 'app-footer',
	templateUrl: './footer.component.html',
	styleUrls: ['./footer.component.scss']
})
export class FooterComponent implements OnInit {

	showFooter : boolean = false

	constructor() { }

	ngOnInit() {
		setTimeout(() => {
			this.showFooter = true
		}, 1500);
	}
}



