import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import { Observable } from 'rxjs/Rx';

@Injectable()
export class StatisticsService {

	constructor(private http: Http) {}

	getNetworkGroup(name: string) : Observable<any> {
		const url = 'http://localhost:3000/api/statistics/network/' + name + '/projects'

		return this.http.get(url)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	getNetworksProfiles(name: string) : Observable<any> {
		const url = 'http://localhost:3000/api/statistics/network/' + name + '/allprofiles'

		return this.http.get(url)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	getNetworksProjects(name: string) : Observable<any> {
		const url = 'http://localhost:3000/api/statistics/network/' + name + '/allprojects'

		return this.http.get(url)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	getUserStatistics(profile_id: number) : Observable<any> {
		const url = 'http://localhost:3000/api/statistics/profiles/' + profile_id

		return this.http.get(url)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}
}



