import { Injectable } from '@angular/core';
import { Response, RequestOptions, Headers } from '@angular/http';
import { Observable } from 'rxjs/Rx';
import { AuthHttp } from 'ng2-bearer';

@Injectable()
export class ArticlesService {

	constructor(private AuthHttp: AuthHttp) { }

	// Add article's tag
	addTag(body: Object, article_id: number) : Observable<any> {
		const url = 'http://localhost:3000/api/articles/' + article_id + '/article_tags'

		return this.AuthHttp.post(url, body)
			.map( (res:Response) => { console.log(res) })
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	// Get all articles
	getArticles() : Observable<any> {
		const url = 'http://localhost:3000/api/articles'
		return this.AuthHttp.get(url)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	// Create new article
	createArticle(body: Object) : Observable<any> {
		const url = 'http://localhost:3000/api/articles'

		return this.AuthHttp.post(url, body)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	// Update article
	updateArticle(article_id: number, body: Object) : Observable<any> {
		const url = 'http://localhost:3000/api/articles/' + article_id

		return this.AuthHttp.put(url, body)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	// Remove article
	deleteArticle(article_id: number, body: Object) : Observable<any> {
		let headers = new Headers({ 'Content-Type': 'application/json' });
		let options = new RequestOptions({ headers: headers });
		const url = 'http://localhost:3000/api/articles/' + article_id

		return this.AuthHttp.delete(url, new RequestOptions({
			headers : headers,
			body 	: body
		}))
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	// Remove article's tag
	deleteTag(article_id: number, article_tag_id: number) : Observable<any> {
		const url = 'http://localhost:3000/api/articles/' + article_id + '/article_tags/' + article_tag_id

		return this.AuthHttp.delete(url)
			.map( (res:Response) => { console.log(res) })
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	/* ARTICLES COMMENTS */
	getArticleComments(article_id: number) : Observable<any> {
		const url = 'http://localhost:3000/api/articles/comment/' + article_id

		return this.AuthHttp.get(url)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	createArticleComments(article_id: number, body: Object) : Observable<any> {
		const url = 'http://localhost:3000/api/articles/comment/' + article_id
		console.log(body)
		return this.AuthHttp.post(url, body)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

}



