import { Injectable } from '@angular/core';
import { Http, Response, RequestOptions, Headers } from '@angular/http';
import { Observable } from 'rxjs/Rx';
import { AuthHttp } from 'ng2-bearer';

@Injectable()
export class NetworksService {

	constructor(private AuthHttp: AuthHttp) { }

	getNetworks(from: string) : Observable<any> {
		const url = 'http://localhost:3000/api/networks/' + from

		return this.AuthHttp.get(url)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	getNetworksComplete(from: string) : Observable<any> {
		const url = 'http://localhost:3000/api/networks/' + from + '/complete'

		return this.AuthHttp.get(url)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	joinNetwork(body: Object) : Observable<any> {
		const url = 'http://localhost:3000/api/networks/join'

		return this.AuthHttp.post(url, body)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	validationNetwork(token: string) : Observable<any> {
		const url = 'http://localhost:3000/api/networks/join/' + token

		return this.AuthHttp.get(url)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	createNetworkInvitation(body: Object) {
		const url = 'http://localhost:3000/api/networks/admin'

		return this.AuthHttp.post(url, body)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	getNetworkInvitation(token: string) {
		const url = 'http://localhost:3000/api/networks/admin/' + token

		return this.AuthHttp.get(url)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}


	// getGoogleContactList(token) {
	// 	let headers = new Headers({'Access-Control-Allow-Origin' : '*', 'Content-Type': 'application/json' });
	// 	let options = new RequestOptions({ headers: headers });
	// 	const url 	= 'https://www.google.com/m8/feeds/contacts/default/full?alt=json&access_token=' + token.access_token + '&max-results=10000&v=3.0'

	// 	return this.AuthHttp.get(url, new RequestOptions({
	// 		headers : headers,
	// 	}))
	// 		.map( (res:Response) => res.json())
	// 		.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	// }
}



