import { Injectable } from '@angular/core';
import { Http, Response, RequestOptions, Headers } from '@angular/http';
import { Observable } from 'rxjs/Rx';
import { AuthHttp } from 'ng2-bearer';

@Injectable()
export class BackofficeService {

	constructor(private AuthHttp: AuthHttp) { }

	getStatsAnalytic() : Observable<any> {
		const url = 'http://localhost:3000/api/statistics/backoffice/all'

		return this.AuthHttp.get(url)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	getProjects() : Observable<any> {
		const url = 'http://localhost:3000/api/admin_panel/statistics/projects'

		return this.AuthHttp.get(url)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	getUsers() : Observable<any> {
		const url = 'http://localhost:3000/api/admin_panel/statistics/users'

		return this.AuthHttp.get(url)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	getAmbassadors() : Observable<any> {
		const url = 'http://localhost:3000/api/admin_panel/uc/ambassadors'

		return this.AuthHttp.get(url)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	setAmbassador(body: Object) : Observable<any> {
		const url = 'http://localhost:3000/api/admin_panel/uc/ambassadors'

		return this.AuthHttp.post(url, body)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	removeAmbassador(body: Object) : Observable<any> {
		const url = 'http://localhost:3000/api/admin_panel/uc/ambassadors'

		return this.AuthHttp.put(url, body)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	getSelfPostProjects() : Observable<any> {
		const url = 'http://localhost:3000/api/admin_panel/projects'

		return this.AuthHttp.get(url)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	sendProjectInvitation(body: Object) : Observable<any> {
		const url = 'http://localhost:3000/api/admin_panel/projects'

		return this.AuthHttp.post(url, body)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	updateProjectCreator(token: string, body: Object) : Observable<any> {
		const url = 'http://localhost:3000/api/admin_panel/projects/' + token

		return this.AuthHttp.post(url, body)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	getSelectedProjectsProfiles(type: string) : Observable<any> {
		const url = 'http://localhost:3000/api/admin_panel/selected/projects/profiles/' + type

		return this.AuthHttp.get(url)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	addSelectedProjectsProfiles(type: string, body: Object) : Observable<any> {
		const url = 'http://localhost:3000/api/admin_panel/selected/projects/profiles/' + type

		return this.AuthHttp.post(url, body)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	removeSelectedProjectsProfiles(type: string, body: Object) : Observable<any> {
		let headers = new Headers({ 'Content-Type': 'application/json' });
		const url = 'http://localhost:3000/api/admin_panel/selected/projects/profiles/' + type

		return 	this.AuthHttp.delete(url, new RequestOptions({
					headers : headers,
					body 	: body
				}))
				.map( (res:Response) => res.json())
				.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
		}
}



